import React, {Component} from 'react';
import {Modal, Header, Input, Button, Segment, Container} from 'semantic-ui-react'
import color from '../colors'
const timeoutLength = 2500

class NotesModal extends Component {

    state = { isOpen: false }

    handleOpen = (e) => {
      this.setState({ isOpen: true });
//      this.setState({ isOpen: true },() => setTimeout(() => this.input.focus()),0);
      e.stopPropagation();  
    //   this.timeout = setTimeout(() => {
    //     this.setState({ isOpen: false })
    //   }, timeoutLength)
    }
  
    handleClose = () => {
      this.setState({ isOpen: false });
      // clearTimeout(this.timeout)
    }
   
    handleKeyPress = (e) => {
      if (e.key === 'Enter') {
        this.handleClose();
        this.props.onNotesSubmit(e);
      }
    }

    stopPropagation = (e) => {
      e.stopPropagation();
    }

    render() {
        return (
          <Modal basic 
          style={{width:'100%', position:'fixed', top:0, left:0}}
            dimmer={false}
            size="fullscreen"
            trigger={this.props.trigger}
            open={this.state.isOpen}
            onClose={this.handleClose}
            onOpen={this.handleOpen}
          >
          <div
            style={{width:'100%', height:'100%', borderRadius:0,backgroundColor:'#0000', position:'fixed', top:0, left:0, }}
            onClick={this.handleClose}
          >
            <Modal.Content>
              <Modal.Description>
                <Segment textAlign="center" raised  inverted basic className="black"
                  style={{width:'100%', position:'fixed', top:62, left:0, backgroundColor:'transparent'}}
                >
                  <Header 
                      style={{color:color(this.props.item.tags)}}
                  >
                     {this.props.item.name}
                  </Header>
                  <Container textAlign="left">
                  <Input inverted  size="massive" ref={(r) => this.input = r} className="red" className="transparent"
                    // style={{color:color(this.props.item.tags)}}
                    style={{width:'100%'}}
                    // tabIndex={1}
                    placeholder="Notes"
                    defaultValue={this.props.item.notes}
                    onKeyPress={this.handleKeyPress}
                    onClick={this.stopPropagation}
                  />
                  </Container>
                </Segment>
              </Modal.Description>
            </Modal.Content>
            </div>
          </Modal>          
        )
    }
}

export default NotesModal;




