import React, { Component } from 'react';
import {
    Button,
    List,
    Grid,
    Icon,
    Header,
    Segment,
    TransitionablePortal
} from 'semantic-ui-react';
import color from '../colors';
// import HoldButton from './HoldButton'
import PopupNotes from './PopupNotes'

class ItemDetail extends Component {
    state = {keepKeypadOpen :false};

    keepKeypadOpen = () => {
        this.setState({keepKeypadOpen:!this.state.keepKeypadOpen})
      }
    
    stopPropagation = (e) => {
      // if (!this.state.keepKeypadOpen)
        e.stopPropagation();
    }

  render() {
    return (
      <div>
        <TransitionablePortal 
            open={this.props.open} 
            transition={{animation:'fade down', duration:300}} 
            // closeOnDocumentClick={!this.state.keepKeypadOpen} 
            closeOnDocumentClick={false}
            onClose={this.props.onClose}
        >
        {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', bottom: '0px', zIndex: 5000, width:"100%", height:"100%", borderRadius:0}}
          onClick={this.props.onClose}
        //   onClick={this.state.keepKeypadOpen? null: this.props.onClose}
        > */}
          <Segment textAlign="center" inverted style={{ padding:8, left: '0%', position: 'fixed', top: '0px', zIndex: 5002, width:"100%", height:"54", borderRadius:0}}
            onClick={this.stopPropagation}
          >
          <Grid verticalAlign="middle">
            <Grid.Column width={3}>
              <PopupNotes
                item={this.props.item} 
                trigger={
                  <Button secondary size="big"   icon="edit"/>
                }
                {...this.props}
              />
            </Grid.Column>
            <Grid.Column width={10}>
              <Header as="h2" inverted style={{color:color(this.props.item.tags)}}
                onClick={this.props.itemClicked.bind(this, this.props.item.tags)}
              >
                {this.props.item.name}
                {/* <Header.Subheader >
                    {this.props.item.qty + " " + this.props.item.unit}
                </Header.Subheader> */}
              </Header>
            </Grid.Column>
            <Grid.Column width={3}>
              <Button secondary size="big"  icon="chevron up"
                onClick={this.props.onClose}
              />
            </Grid.Column>
          </Grid>
          </Segment>
          {/* </div> */}
        </TransitionablePortal>
      </div>
    );
  }
}

export default ItemDetail;



