import React, { Component } from 'react';
import './App.css';
import {
  Button,
  // Dropdown,
  // List,
  Icon,
//  Modal,
  Input,
  // Divider,
//  Image,
//  Label,
//  Form,
//  Rail,
//  Search,
  Table, 
  // Grid,
//  Transition,
//  Container,
//  Popup,
//  Header,
  Menu,
  Segment,
  TransitionablePortal
} from 'semantic-ui-react';
//import TransitionGroup from 'semantic-ui-react/dist/commonjs/modules/Transition/TransitionGroup';
import Item from "./components/Item";
import Sidebar from "./components/Sidebar";
import Keypad from "./components/Keypad";
import ItemDetail from "./components/ItemDetail";
import EmailComposer from "./components/EmailComposer";
import Categories from './components/Categories';
import CategoryPortal from './components/CategoryPortal';
import IndexPortal from './components/IndexPortal';

const POSTURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad";
// const FETCHURL = "https://api.jsonbin.io/b/5a7cfa997ecc101273331408";
const FETCHURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad/latest";

var newInput=true;

function titleCase(str) {
  return str.toLowerCase().split(' ').map(function(word) {
    return word.replace(word[0], word[0].toUpperCase());
  }).join(' ');
}

const strCompare = (s1,s2) => {
  const u1 = s1.toUpperCase();
  const u2 = s2.toUpperCase();
  if (u1 < u2)
    return -1;
  if (u1 > u2)
    return 1;
  return 0;
}

const byName = (i1, i2) => {
  return strCompare(i1.name, i2.name);
}

const byTag = (i1, i2) => {
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = strCompare(i1.name, i2.name)
  return compare;
}

const timeCompare = (t1, t2) => {
  let d1 = t1?t1:0
  let d2 = t2?t2:0
  // console.log('d1:',d1, 'd2:',d2);
  if (d1 > d2)
    return 1
  if (d1 < d2)
    return -1
  return 0;
}

const byTimestamp = (i1, i2) => {
  return timeCompare(i1.timestamp, i2.timestamp);
}

const byDateDescThenByTimeAsc = (i1, i2) => {
  function compare(a, b) {
      return a > b || -(a < b);
  }

  var aISO = new Date(i1.timestamp).toISOString(),
      bISO = new Date(i2.timestamp).toISOString();
//  console.log(aISO.slice(0, 10));
  return compare(bISO.slice(0, 10), aISO.slice(0, 10))   // date desc
      || compare(aISO.slice(11, 19), bISO.slice(11, 19)); // time asc
};


const byTagThenByTime = (i1, i2) => { 
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = timeCompare(i1.timestamp, i2.timestamp)
  return compare;
}

class App extends Component {

  state = {
    filter:{name:"none", value:""}, //letter, search-text, category, none 
    searchText: "",
    items: [{name:"", qty:"", unit:"", tags:"", notes:""}],
    openKeypad: false,
    marked:false,
    selectedItem:{name:"", qty:0, unit:"", tags:"", notes:""},
    saved:false,
    changed:false,
    status:"",
    sortMode:"Timestamp",
    reverse:false,
    openEmailComposer:false,
    selectedCategory: "",
    showAddPopup:false,
    showCategoryModal:false,
    showCategoryPortal:false,
    showIndexPortal:false,
    selectedLetter:""
  }


  // Fetch and save **********************

  componentDidMount() {
    fetch(FETCHURL)
        .then(response => response.json())
        .then(data => {
          this.fetchedItems = data.Items;
          // for (let i=0; i<data.Items.length; i++)
          //   delete data.Items[i].timestamp; 
          this.setState({items:data.Items})}
        );
  }

  fetchedItems = {};

  changed = () => JSON.stringify(this.fetchedItems) === JSON.stringify(this.state.items);

  handleSaveSendButtonClick = () => {
    if (this.state.changed) {
      this.sendData();
      return;
    }
    this.setState({openEmailComposer:true})
  }

  closeEmailComposer = () => {
    this.setState({openEmailComposer:false})
  }

  sendData = () => {
    this.setState({status:"saving"})
    this.postData(POSTURL, {Date: (new Date()).toJSON(), Items: this.state.items})
    .then(data => {
//      console.log(data);
      if (!data.success)
        throw new Error("Save failed");
      this.fetchedItems = data.data.Items;
//      console.log(this.changed());
      this.setState({saved:true, changed:false, status:"saved"})
      setTimeout(() => {
        this.setState({ saved: false, status:"" })
      }, 500)
    }) // JSON from `response.json()` call
    .catch(error => {
      this.setState({ saved: false, status:"error" })
      console.error(error)
    })
  }

  postData(url, data) {
    return fetch(url, {
      body: JSON.stringify(data), // must match 'Content-Type' header
      headers: {
        'content-type': 'application/json'
      },
      method: 'PUT',
    })
    .then(response => response.json()) // parses response to JSON
  }

// Item ******************************

  deleteItem = (item, e) => {
    const items = Object.assign([], this.state.items);
    const index = items.indexOf(item);
    items.splice(index,1);
    this.setState({items:items});
  }

  addItem = (category) => {
    console.log("addItem: ", category);
//    console.log(this.state.searchText);
    console.log(this.state.newItemName);
    const newItemName = titleCase(this.state.newItemName);
    if (category) {
      const items = Object.assign([], this.state.items);
      const item = { name:newItemName, qty:"", unit:"", tags:category };
      items.push(item);
      this.setState({items:items, newItemName:"", showCategoryModal:false, selectedItem:item, openKeypad:true});
    }
  }

  changeItemName = (e) => {
    console.log(e.target.value);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.name = e.target.value;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  cancelAdd = () => {
    console.log("cancelAdd");
    this.setState({showCategoryModal:false})
  }

  handleAddClick = () => {
    this.setState({newItemName: this.filterValue(), showCategoryModal:true});
  }

  handleGridButtonClick = () => {
    this.setState({showCategoryPortal:true});
  }

  closeCategoryPortal = () => {
    this.setState({showCategoryPortal:false, showCategoryModal:false, newItemName:""});
  }

  handleIndexButtonClick = () => {
    this.setState({showIndexPortal:true});
  }

  closeIndexPortal = () => {
    this.setState({showIndexPortal:false});
  }

  handleClick = (item,index,e) => {
//    console.log("handleClick: " + item.name);
    // console.log (document.activeElement);
    // console.log(this.searchRef.inputRef);
    if (this.searchRef.inputRef === document.activeElement)
      return;
//      Without setTimeout the modal has not rendered when focus() is called.
//      setTimeouts() are executed last in the function all stack so 0 delay works!
    this.setState({selectedItem:item});//, () => setTimeout(() => this.ref.focus(),0));
    this.openKeypad();
    newInput=true;
  }

  selectItem = (item,e) => {
//    console.log("selectItem: " + e.value);
    const index = this.findItemIndexByName(e.value);
//    console.log(index);
    const _item = this.state.items[index];
    this.setState({selectedItem:_item});
  }
 
// Filter ************************************

  setFilter = (name, value) => {
    console.log('setFilter', name);
    this.setState({filter:{name:name, value:value}})
  }

  clearFilter = () => {
    this.setFilter("none", "");
  }

  isFilteredBy = (name) => {
    const filter = this.state.filter;
    return (filter.name === name && filter.value.length > 0)
  }

  isFiltered = () => {
//    console.log("isFiltered:", this.state.filter);
    return (this.state.filter.name !== "none");
  }
  filterValue = () => {
    return (this.state.filter.value);
  }

// Search ************************************

  changeSearchText = (e) => {
//    console.log(e.target.value);
      if (e.target.value.length > 0) {
        this.setFilter("search-text", e.target.value);
        // this.setSortMode("None");
      }
      else {
        this.clearSearchText(e);
      }
  }

  clearSearchText = (e) => {
    e.target.value = "";
    this.searchRef.value = "";
    this.clearFilter();
    //this.setState({searchText: ""});
  }
  

  submitSearch = (addable,e) => {
//    console.log(addable);
    if (e.key === "Enter") {
      console.log("enter pressed");
      if (addable)
        this.handleAddClick();
    }
  }

  focusSearch = () => {
      this.searchRef.focus();
  }


  sendClicked = (e) => {
    this.setState({openEmailComposer: !this.state.openEmailComposer});//, () => setTimeout(() => this.ref.focus(),0));
  }

// Keypad *********************

  openKeypad = () => {
    this.setState({openKeypad: true});
  }

  closeKeypad = () => {
    if (this.state.openKeypad) {
      this.setState({openKeypad: false});
    }
  }


// Qty + Unit **************************

  toggleMarked = (item,e) => {
//    console.log(e.children);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.marked = !_item.marked;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  setQty = (item,e) => {
//    console.log(e.children);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (newInput || _item.qty.length > 1 || _item.qty==="0") {
      _item.qty="";
    }
    _item.qty = _item.qty + e.children;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item, changed:true});
    this.searchRef.value="";
    newInput=false;
  }

  clearQty = (e) => {
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.qty = "";
    _item.unit = "";
    _item.marked = "";
    delete _item.timestamp;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  setUnit = (item,e) => {
    const newUnit = e.children;
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (_item.unit === newUnit)
      _item.unit = "";
    else
      _item.unit = newUnit;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  setNote = (item,notes) => {
//    console.log("setNote", item, notes);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.notes = notes;
//    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  findItemIndexByName = (itemName) => {
    for (let i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].name === itemName)
        return i;
    }
    return -1;
  }


  statusToColor = () => {
    let ret = "black";
    switch (this.state.status) {
      case "saving" :
        ret = "black";
        break;
      case "saved" :
        ret = "green";
        break;
      case "error" :
        ret = "red";
        break;
      default :
        ret = "black";
    }
    return ret;
  }

// Sort ************************

  setSortMode = (sortMode) => {
    console.log("setSortMode", sortMode);
    this.setState({sortMode:sortMode});
  }

  getSortIconName = () => {
    let sortMode = this.state.sortMode;
    let sortIcon = "sort numeric";
    if (this.isFiltered())
      sortIcon = 'arrow left';
    else {
      switch (sortMode) {
        case "Alphabetical":
          sortIcon = 'sort alphabet ascending';
          break;
        case "Grouped":
          sortIcon = 'sort content ascending';
          break;
        case "Timestamp":
          sortIcon = 'sort numeric ascending';
          break;
        default:
          sortIcon = 'question';
      }
    }
    if (this.state.reverse) 
      sortIcon = sortIcon.replace('ascending',  'descending');
    return sortIcon;
  }

  reverse = () => {
    let rev = !this.state.reverse;
    const _items = Object.assign([], this.state.items);
//    _items.reverse();
    this.setState({items:_items, reverse:rev});
  }

  handleSortClick = (e) => {
//    console.log("handleSortClick: ", e.target.innerHTML);
    let sortMode = this.state.sortMode;
    if (this.isFiltered()) {
      this.clearFilter();
    }
    else if (sortMode === "Timestamp") {
      sortMode = 'Alphabetical';
    }
    else if (sortMode === 'Alphabetical') {
      sortMode = 'Grouped';
    }
    else { 
      sortMode = 'Timestamp';
    }      
//    console.log(".newsortMode", sortMode);
    this.setSortMode(sortMode);
  }

  // setSortMode = (sortMode,e) => {
  //   this.setSortMode(sortMode);
  // }
    

// AZ *****************************************  
  selectLetter = (p,e) => {
//    console.log(p);
    e.stopPropagation();
    if (this.isFilteredBy("letter") && this.filterValue() === p) {
      this.clearFilter();
    }
    else {
      this.setFilter("letter", p);
    }
  }

  selectCategory = (category) => {
    if (this.isFilteredBy("category") && this.filterValue() === category) {
      this.clearFilter();
    }
    else {
      this.setFilter("category", category);
    }
  }

  handleNavClick = (prevItem, e) => {
//    console.log(prevItem.name);
    this.setState({selectedItem: prevItem });
    newInput=true;
  }

  handleNotesSubmit = (item,e) => {
//    console.log(e.key);
    // if(e.key === 'Enter'){
      console.log('enter press here! ')
      const items = Object.assign([], this.state.items);
      const index = this.findItemIndexByName(item.name);
      const _item = items[index];
      _item.notes = e.target.value;
      _item.marked = (_item.notes.length > 0);
      items[index] = Object.assign({},_item);
      this.setState({items:items});
//      console.log(_item);
//      console.log(items[index]);
    // }
  }

  handleNotesSubmit = (item,e) => {
      const items = Object.assign([], this.state.items);
      const index = this.findItemIndexByName(item.name);
      const _item = items[index];
      _item.notes = e.target.value;
      _item.marked = (_item.notes.length > 0);
      items[index] = Object.assign({},_item);
      this.setState({items:items});
//      console.log(_item);
//      console.log(items[index]);
  }



  // Render ************************
  render() {
    let sort = true;
    const filteredItems = this.state.items.filter((item, index) => {
      if (this.isFilteredBy("completed")) {
        console.log("fsdfsd");
        return (item.qty.length > 0 && (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date().setHours(0,0,0,0)))
      }
      if (this.isFilteredBy("category")) 
        return (item.tags === this.state.filter.value)
      if (this.isFilteredBy("letter")) 
        return (item.name.charAt(0).toUpperCase() === this.state.filter.value.charAt(0).toUpperCase())
      if (this.isFilteredBy("search-text")) {
        if (this.state.filter.value.length === 1) 
          return (item.name.charAt(0).toUpperCase() === this.state.filter.value.charAt(0).toUpperCase())
        if (this.state.filter.value.length > 1) 
          return (item.name.toUpperCase().includes(this.state.filter.value.toUpperCase()))
      }
      sort = false;  
      return (item.qty || item.unit || item.marked)
    });
//    let sortedItems = [];
    if (sort)
      filteredItems.sort(byName)
    else {
      const sortMode = this.state.sortMode;
      if (sortMode === 'Alphabetical')
        filteredItems.sort(byName);
      if (sortMode === 'Grouped')
        filteredItems.sort(byTag);
      if (sortMode === 'Timestamp')
        filteredItems.sort(byDateDescThenByTimeAsc);
      if (this.state.reverse)
        filteredItems.reverse();
    }
    const items = filteredItems.map((item,index) => {
      return (
        <Item
          key={item.name}
          item={item}
          selectedItem={this.state.selectedItem}
          index={index}
          // selectedItem={this.state.selectedItem}
          selected={(item.name === this.state.selectedItem.name)}
          deleteEvent={this.deleteItem.bind(this, item)}
          rowClickEvent={this.handleClick.bind(this, item, index)}
          onNotesSubmit={this.handleNotesSubmit.bind(this, item)}
        > {item.name}</Item>)
    })

    const addable = items.length === 0 && this.isFilteredBy('search-text') && this.state.filter.value.length > 3 || this.state.showCategoryModal  ;
    // const currentItemIndex=filteredItems.indexOf(filteredItems.find(item => item.name === this.state.selectedItem.name));
    const currentItemIndex=filteredItems.indexOf(this.state.selectedItem);
    let prevItem, nextItem, prevDisabled, nextDisabled;
    if (currentItemIndex > 0) {
      prevItem = filteredItems[currentItemIndex - 1];
      prevDisabled=false;
    }
    else {
      prevItem = this.state.selectedItem;
      prevDisabled=true;
    }
    if (currentItemIndex < items.length - 1) {
      nextItem = filteredItems[currentItemIndex + 1];
      nextDisabled=false;
    }
    else {
      nextItem = this.state.selectedItem;
      nextDisabled=true;
    }
          
//    console.log( JSON.stringify(currentItemIndex));
    let sidebarView = Sidebar.View.keypad;
    if (this.state.showCategoryModal)
      sidebarView = Sidebar.View.categories;

    return (
      <div>
        <TransitionablePortal basic open={addable} transition={{animation:"fade", duration: 250}}>
          <Segment inverted className="hidden" textAlign="center" style={{borderRadius:0, padding:12, position:'fixed',zIndex:6000, top:0, right:0}}>
            <Button basic inverted onClick={this.handleAddClick}>
              Add
               {/* <em><b style={{color:'yellow'}}>{this.state.filter.value}</b></em> */}
            </Button>
          </Segment>
        </TransitionablePortal>
        <Sidebar
          items={this.state.items}
          item={this.state.selectedItem}
          open={this.state.openKeypad || this.state.showCategoryModal}
          view={sidebarView}
          letterSelected={this.selectLetter}  
          categorySelected={this.selectCategory}
          sortModeSelected={this.setSortMode}
          sortDirectionChanged={this.reverse}
          reverse={this.state.reverse}
          // onClick={this.closeCategoryPortal}     
          // onClose={this.closeCategoryPortal}                   
          // itemClicked={this.selectCategory}
          // prevDisabled={prevDisabled}
          // prevClicked={this.handleNavClick.bind(this, prevItem)}
          // nextDisabled={nextDisabled}
          // nextClicked={this.handleNavClick.bind(this, nextItem)}
          // open={this.state.openKeypad}
          // onClose={this.closeKeypad}
          numberClicked={this.setQty}
          unitClicked={this.setUnit}
          deleteClicked={this.clearQty}
          asteriskClicked={this.toggleMarked}
          showSimilar={this.selectCategory}
          // noteEntered={this.setNote}
        />
        <CategoryPortal items={this.state.items}
          open={this.state.showCategoryPortal || this.state.showCategoryModal}
          categorySelected={this.selectCategory}
          onClick={this.closeCategoryPortal}     
          onClose={this.closeCategoryPortal}     
        />
        {(this.state.showCategoryModal && this.isFilteredBy("category")) ?
          <Button secondary size="massive" icon="add"
            style={{position:'fixed', left:'15%', top:'50%', zIndex:100000}}
            onClick={this.addItem.bind(this, this.filterValue())}
          /> :
          null
        }
        {/* <IndexPortal items={this.state.items}
          open={this.state.showIndexPortal}
          onClose={this.closeIndexPortal}
          letterSelected={this.selectLetter}      
        />        
        <Categories
          items={this.state.items} 
          newItemName={this.state.filter.value}
          open={this.state.showCategoryModal}
          open={false}
          positiveClickHandler={this.addItem}
          negativeClickHandler={this.cancelAdd}
        /> */}
        <EmailComposer 
          items={this.state.items
            .filter((item,index) => (item.qty.length > 0 && 
              (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date()).setHours(0,0,0,0)))
            .sort(byTagThenByTime)}
          open={this.state.openEmailComposer}
          onClose={this.closeEmailComposer}
        />
        <Menu size="massive" widths={16} className="ui fixed compact inverted" style={{height:62}}>
          <Menu.Item>
            <Input size="huge" ref={searchRef => this.searchRef = searchRef} 
            // <Input disabled={this.state.sortMode === "None" && this.isFilteredBy('search-text')} size="huge" ref={searchRef => this.searchRef = searchRef} 
              inverted transparent style={{color:'white',width:'60%'}}            
              onKeyPress={this.submitSearch.bind(this, addable)}
              onChange={this.changeSearchText}
              onFocus={this.clearSearchText}
              onClose={this.clearSearchText}
              value={this.isFilteredBy("search-text") ? this.filterValue() : this.state.newItemName}
              type="text"
            />
          </Menu.Item>
          {/* <Menu.Item position="right" onClick={this.sendData}>
            <Icon link name="send" circular/>
          </Menu.Item> */}
          <Menu.Item position="right"  style={{width:72, position:'fixed', top:0, right:0}}>
            {/* <Button size="huge" secondary icon="check" onClick={this.setFilter.bind(this, 'completed',"*")}/> */}
            <Button size="huge" secondary icon="chevron right" onClick={this.closeKeypad}/>
            {/* <Button size="huge" secondary icon={this.getSortIconName()} onClick={this.handleSortClick}/> */}
          </Menu.Item>
        </Menu>
        <Button size="huge" secondary  
          icon={addable?"add":"search"} 
          style={{position: 'fixed', top:6, left:6, display:'block', zIndex:700  }} onClick={this.focusSearch}
        />
        {/* <Button.Group className="black" vertical basic style={{position: 'fixed',top:80, right:10, display:'block', zIndex:700, opacity:0.7, borderRadius:0,boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)" }}>
          <Button size="huge" icon="sort" onClick={this.reverse} style={{borderRadius:0}} />
          <Button size="huge" icon={<Icon>AZ</Icon>}  onClick={this.handleIndexButtonClick} style={{borderRadius:0}}/>
          <Button size="huge" icon="grid layout" onClick={this.handleGridButtonClick} style={{borderRadius:0}}/>
        </Button.Group> */}
        <Button color={this.statusToColor()} size="massive" circular icon={this.state.changed?"save":"check"}
           style={{boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)", position: 'fixed', bottom:24, right:16, display:'block', zIndex:700  }}
           onClick={this.handleSaveSendButtonClick}
        />
        <Table inverted unstackable selectable={false} striped={false} singleLine fixed width={16} style={{marginTop:62, marginBottom:'100%'}}>
          <Table.Body>
            {items}
          </Table.Body>
        </Table>
        {/* <Popup basic inverted  open={this.state.saved} style={{position:'fixed', width:'50%', top:'25%', left:'25%'}}><Container textAlign="center">Saved!</Container></Popup> */}
        {/* <Keypad
          item={this.state.selectedItem}
          itemClicked={this.selectCategory}
          prevDisabled={prevDisabled}
          prevClicked={this.handleNavClick.bind(this, prevItem)}
          nextDisabled={nextDisabled}
          nextClicked={this.handleNavClick.bind(this, nextItem)}
          open={this.state.openKeypad}
          onClose={this.closeKeypad}
          numberClicked={this.setQty}
          unitClicked={this.setUnit}
          deleteClicked={this.clearQty}
          asteriskClicked={this.toggleMarked}
          noteEntered={this.setNote}
        /> */}
        {/* <ItemDetail
          item={this.state.selectedItem}
          open={this.state.openKeypad}          
          onClose={this.closeKeypad}
          itemClicked={this.selectCategory}
          noteEntered={this.setNote}          
        /> */}
      </div>
    );
  }
}

export default App;



