import React, { Component } from 'react';
import {
    Button,
    List,
    Grid,
    Icon,
    Header, 
    Segment,
    Label,
    Container,
    TransitionablePortal
} from 'semantic-ui-react';
import IndexPortal from './IndexPortal';
import CategoryPortal from './CategoryPortal';
import SortPortal from './SortPortal';
import Keypad from './Keypad';

class Sidebar extends Component {

    static View = Object.freeze ({ keypad: {}, letters: {}, categories: {}, sorted: {} });
    
    state = {view:this.props.view};

    setView = (view) => {
        console.log(view);  
        this.setState({view:view});
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.item.name !== this.props.item.name) {
            this.setView(Sidebar.View.keypad);
        }
    }
    
    render() {
        
        const View = Sidebar.View;
        
        return (
        <div>
            {console.log(this.props.view)}
            <TransitionablePortal 
                open={this.props.open} 
                transition={{animation:'fade left', duration:500}} 
                closeOnDocumentClick={false}
//                onClose={this.props.onClose}
            >
                <Segment textAlign="center" inverted basic 
                    className="ui black"
                    style={{
                        zIndex: 5001,
                        backgroundColor: '#3A3A3A',
                        position: 'fixed',
                        top:54,
                        right: '0%', 
                        width:"50%",
                        height:"calc(100% - 54px",
                        padding:'20px 0px',
                        borderStyle:'none',
                        borderRadius:0
                    }}
                    onClick={this.stopPropagation}
                >
                    <Label className="black" attached="bottom" style={{padding:'7px 0px'}}>
                        <Button.Group size="huge"inverted basic style={{opacity:0.7}}>
                            {/* <Button size="huge" icon="sort" onClick={this.setView.bind(this, View.keypad)} style={{borderRadius:0}} /> */}
                            <Button icon="sort"
                                onClick={this.setView.bind(this, View.sorted)}
                                style={{borderRadius:0}} 
                            />
                            <Button icon={<Icon>AZ</Icon>}
                                onClick={this.setView.bind(this, View.letters)}
                                style={{borderRadius:0}}
                            />
                            <Button icon="grid layout"
                                onClick={this.setView.bind(this, View.categories)}
                                style={{borderRadius:0}}
                            />
                        </Button.Group>
                    </Label>
                    <IndexPortal {...this.props} open={this.state.view === View.letters}/>
                    <CategoryPortal {...this.props} open={this.state.view === View.categories}/>
                    <SortPortal {...this.props} open={this.state.view === View.sorted}/>
                    <Keypad {...this.props} open={this.state.view === View.keypad}/>                
                </Segment>
            </TransitionablePortal>
        </div>
        );
    }
}

export default Sidebar;



