import React, { Component } from 'react';
import {TransitionablePortal, Modal, Button,Container, Accordion, Segment, Header} from 'semantic-ui-react';
import color from '../colors';
import CategoryItems from './CategoryItems'

class Categories extends Component {

    state = { activeIndex: -1, category: "" }

    handleClick = (e, titleProps) => {
        console.log(titleProps);
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index
    
        this.setState({ activeIndex: newIndex, category: titleProps.children })
    }
    
    render() {  
        const categories = [...new Set(this.props.items.map(item => item.tags))];
        const renderedCategories = categories.map((category,index) => {
            return (
                <Accordion key={index} styled inverted fluid style={{borderRadius:0,backgroundColor: color(category), padding:10}}>
                    <Accordion.Title index={index} active={this.state.activeIndex === index} onClick={this.handleClick}>
                        {/* <Icon name='dropdown' />                         */}
                        {category}
                    </Accordion.Title>
                    <Accordion.Content active={this.state.activeIndex === index} category={category}> 
                        <CategoryItems category={category} items={this.props.items}/>
                    </Accordion.Content>
                </Accordion>
            )
        });  
        return (
            <div>
                <TransitionablePortal
                    open={this.props.open}
                >
                    <Segment 
                        inverted
                        className="black"
                        style={{position:'fixed', top:0, left:0, width:'100%', height:'100%', zIndex:100000}}
                    >
                        <Header>
                            Select a category:
                        </Header>
                        <div style={{height:'80%',overflow:'scroll'}}>
                            {renderedCategories}
                        </div>
                        <Container textAlign="right"    >
                            <Button 
                                onClick={this.props.negativeClickHandler}
                            >
                                Cancel
                            </Button>
                            <Button style={{color:(this.state.category.length>0)?"white":"black", backgroundColor: color(this.state.category)}}
                                onClick={this.props.positiveClickHandler.bind(this, this.state.category)}
                            >
                                Add {this.props.newItemName}
                            </Button>                        
                        </Container>
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default Categories;



