import React, { Component } from 'react';
import {TransitionablePortal, Segment, Label} from 'semantic-ui-react';
import color from '../colors';
// import CategoryItems from './CategoryItems'

class CategoryPortal extends Component {

    state = { activeIndex: -1, category: "" }

    handleClick = (e, titleProps) => {
        console.log(titleProps);
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index

        this.setState({ activeIndex: newIndex, category: titleProps.children })
    }

    // stopPropagation = (e) => {
    //     e.stopPropagation();
    //   }

    render() {
        const categories = [...new Set(this.props.items.map(item => item.tags))].sort();
        const renderedCategories = categories.map((category,index) => {
            return (
                <span key={index}>
                    <Label onClick={this.props.categorySelected.bind(this,category)} style={{width:'50%', height:36, margin:0, backgroundColor:'transparent', color:color(category)}}>{category}</Label>
                </span>
            )
        });
        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                >
                    {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '62px', zIndex: 5002, width:"100%", height:"100%", borderRadius:0}}
                        onClick={this.props.onClose}
                    > */}
                    <Segment inverted textAlign="center"
                        style={{ borderRadius:"0em", right: '0%', margin:0,  position: 'fixed', top: '54px', zIndex: 5004, width:"50%", height:"auto"}}
                        // onClick={this.stopPropagation}
                    >
                        {renderedCategories}
                    </Segment>
                    {/* </div> */}
                </TransitionablePortal>
            </div>
        );
    }
}

export default CategoryPortal;



